// import classNames from 'classnames';
import React, { useCallback, useMemo, useState } from 'react';
import { createStore, /* createStyleSet */ } from 'botframework-webchat';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

import WebChat from './WebChat';

import MessageCloud from './message-cloud.png';

import './fabric-icons-inline.css';
import './MinimizableWebChat.css';

const useStyles = makeStyles((theme) => ({
  title1: {
    margin: '0px 0px 0px 20px',
    fontFamily: 'Proxima Nova Regular, sans-serif',
    fontSize: '27px',
    color: '#FFFFFF',
    fontWeight: '700',
  },
  title2: {
    margin: '5px 0px 5px 20px',
    fontFamily: 'Proxima Nova Regular, sans-serif',
    fontSize: '15px',
    color: '#FFFFFF',
    fontWeight: '200',
  },
  emptyMainTypography: {
    flexGrow: 1,
  },
  headerIconButton: {
    padding: '10px 10px 10px 10px',
  },
  avatarIcon: {
    width: '55px',
    height: '55px',
    margin: '0px 10px 0px 10px',
  },
  messageCloud: {
    width: '80px',
    height: '80px',
    position: 'absolute',
    zIndex: '1500',
    bottom: '50px',
    right: '50px',
  },
  messageCloudHeader: {
    margin: '20px 0px 10px 40px ',
    width: '60px',
    height: '60px',
  },
}));


const MinimizableWebChat = (props) => {

  const classes = useStyles();

  const store = useMemo(
    () =>
      createStore({}, ({ dispatch }) => next => action => {
        if (action.type === 'DIRECT_LINE/CONNECT_FULFILLED') {
          dispatch({
            type: 'WEB_CHAT/SEND_EVENT',
            payload: {
              name: 'webchat/join',
              value: {
                language: 'window.navigator.language'
              }
            }
          });
        }
        /* else if (action.type === 'DIRECT_LINE/INCOMING_ACTIVITY') {
          if (action.payload.activity.from.role === 'bot') {
            // setNewMessage(true);
          }
        } */

        return next(action);
      }),
    []
  );

  const [token, setToken] = useState();

  // To learn about reconnecting to a conversation, see the following documentation:
  // https://docs.microsoft.com/en-us/azure/bot-service/rest-api/bot-framework-rest-direct-line-3-0-reconnect-to-conversation?view=azure-bot-service-4.0

  const handleFetchToken = useCallback(async () => {
    if (!token) {
      // const res = await fetch('https://e-councilhr.azurewebsites.net/directline/token', { method: 'POST' }, { mode: 'no-cors'});
      const res = await fetch(
        'https://webchat.botframework.com/api/tokens',
        {
          method: 'GET',
          headers: {
            'Authorization': 'BotConnector KxH5mTB3KH0.GOS2JFASIiLQlZttpBQUsxBmHjn-SbtS73Q7gKlh8ZA',
          }
        });
      let token = await res.text(); setToken(token.substring(1, token.length - 1));
    }
  }, [setToken, token]);

  return (
    <div className="minimizable-web-chat">
      <div className={'chat-box'}>
        <header>
          <div>
            <img
              src={MessageCloud}
              alt={''}
              className={classes.messageCloudHeader}
            />
          </div>
          <Typography className={classes.title1} >
            {'Hazza'}
          </Typography>
          <Typography className={classes.title2} >
            {'Your MoIAT Digital Assistant'}
          </Typography>
        </header>
        <WebChat
          className="react-web-chat"
          onFetchToken={handleFetchToken}
          store={store}
          token={token}
        />
      </div>
    </div>
  );
};

export default MinimizableWebChat;
